@import url('https://fonts.googleapis.com/css?family=Oswald:300,400&display=swap');

@import 'bootstrap/bootstrap';
@import 'functions';
@import 'menunav';
@import'modalcard';
@import 'variables';

// @import 'mixins';
// @import 'socials';
 

// * {
//     box-sizing: border-box;
//     margin: 0;
//     padding: 0;
// }

a, button {
    transition: all .4s ease-in;
    text-decoration: none;
}

li {
    list-style: none;
}

html {
    height: 100%;
}

body {
    height: 100%;
    overflow-x: hidden;
    scroll-behavior: smooth;
    position: relative;
}

@mixin desktop {
    @media screen and (max-width: 1200px) {
         @content;
    }
}

@mixin laptop {
    @media screen and (max-width: $laptop) {
      @content;
    }
  }
  
 @mixin tablets {
     @media screen and (max-width: $tablets) {
      @content;
    }
  }
  
 @mixin phones {
    @media screen and (max-width: $phones) {
      @content;
    }
  }

h1 {
    letter-spacing: 13.5px;
    text-shadow: 12px 12px 30px rgba($color: #000000, $alpha: 0.8);
    color: $white;
    @include phones {
        letter-spacing: 5px;
    }
}
h3 {
    letter-spacing: 4.5px;
    font-weight: 400;
    @include phones {
        letter-spacing: 1px;
    }
}

.btn {
    @include phones {
        padding: 5px;
    }
    span {
        color: $white;
        @include phones {
            font-size: 1rem;
        }
        &::after {
            content: '';
            position: absolute;
            display: block;
            top: 50%;
            left: 50%;
            transform: translate(80%, 0);
            width: 50px;
            height: 3px;
            background-color: rgba($color: #ffffff, $alpha: .8);
        }
    }
}
 

.intro {
    position: relative;
    background: url(../img/intro1.jpg) #abcbe2 center center no-repeat;
    background-size: cover;
    &::before {
        content: "";
        // display: block;    
        width: 100%;
        height: 100%;    
        position: absolute;
        top: 0;      
        left: 0;   
        background: transparent;
        background-image: linear-gradient(to bottom, rgba(0,0,0, 0.5), rgba(0,0,0, .1));
        opacity: 0.8;
    }
    .nav-link {
        font-size: 18px;
        color: rgba($color: #ffffff, $alpha: 0.8);
        letter-spacing: 3.5px;
        &::after {
            content: '→';
            display: inline-block;
            margin-left: 1.3rem;
            height: 3px;
        }
        &:hover,
        &:focus, 
        &:active {
            color: $primary;
            opacity: 1;
        }
    }
    .line-health {
        font-size: 26px;
        font-family: "Oswald";
        color: rgb(255, 255, 255);
        text-transform: uppercase;
        line-height: 2.115;
        transform: matrix( 0,-1,1,0,0,0);
        position: absolute;
        left: -2%;
        top: 72%;
        display: inline-block;
        @include phones {
           display: none;
        }
        .line {
          position: absolute;
          top: -50%;
          left: -50%;
          transform: matrix( 0,-1,1,0,0,0);
          @include phones {
              display: none;
          }
        }
    }
    h3 {
        letter-spacing: 7px;
        font-weight: 300;
        color: $white;
       
    }
    .scroll {
        position: absolute;
        display: block;
        text-align: center;
        padding: 7px;
        left: calc(50% - 30px);
        top: 90%;
        width: 60px;
        height: 60px;
        .svg-icon {
            width: 40px;
            height: 40px;
            fill: $white;
        }
    }
    .btn {
        border-radius: 0;
        padding: 20px 16px;
    }
}

.scroll:hover .svg-icon,
.scroll:focus .svg-icon,
.scroll:active .svg-icon {
    fill: $primary;
    transform: translate(0, 15%);
} 
  

.about {
    &__bordered {
        background: rgb(106, 114, 76);
        background: radial-gradient(ellipse at center, #CED6AF 0%, #CED6AF 50%, #6E774A 100%);
        .logo {
            margin: 0;
            picture {
                img {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    max-width: 100%;
                    height: auto;
                }
            }
        }
    }
    &__line {
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
     }
    .nav {
        border-bottom: none;
        margin-left: -1rem;
        position: relative;
        &::before {
            content: '';
            position: absolute;
            display: block;
            width: 100%;
            top: 100%;
            left: 0;
            height: 2px;
            background-color: $teal;
        }
        &__link {
            letter-spacing: 4.5px;
            @include tablets() {
                font-size: 1.3rem;
                padding-bottom: 0;
                letter-spacing: 1px;
            }
            @include phones()  {
                font-size: 1.1rem;
            }
        }
    }
     .absolute {
        transform: translate(25%, 0);
        @include tablets {
            transform: translate(0, 0);
        }
     }

    &__image {
        width: 100%;
        margin: 0;
        .position-relative {
           padding-top: 50%;
           overflow: hidden;
           img {
               top: 50%;
               left: 50%;
               transform: translate(-50%, -50%);
               min-height: 100%;
           }
     }
    }
    p {
        font-weight: 300;
        line-height: 2.222;
    }
     
}


.featured-products {
    .card {
        display: block;
        width: 100%;
        height: 500px;
        background-color: $white;
        overflow: hidden;
        border: 1px solid $primary;
        letter-spacing: 3px;
        text-decoration: none;
        &:hover,
        &:focus,
        &:active {
            background-color: rgba(110, 119, 74, .2);
        }
        &:hover .card__image {
            top: 0;
            left: 50%;
            transform: translate(-50%, 50%) scale(1.05);
        }
        &__image {
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
        &__cena {
            left: 0;
            right: 0;
            bottom: 0;
            background-color: $gray-200;
            span {
            &:nth-of-type(2){
                    &::after {
                        content: '';
                        display: block;
                        position: absolute;
                        width: 20%;
                        height: 2px;
                        background-color: $white;
                        top: 50%;
                        left: 50%;
                        transform: translate(30%, -50%);
                        @include tablets {
                            width: 20px;
                            left: 60%;
                        }
                    }
                }
            }
         }
        }
        &__icon {
            width: 20px;
            height: 20px;
            position: relative;
            img {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                max-width: 100%;
            }
            .minus {
                background-image: url(../img/minus.png);
                background-size: cover;
                width: 20px;
                height: 20px;
            }
        }
        .button-prev,
        .button-next {
          position: absolute;
          top: 89%;
          width: 50px;
          height: 50px;
          z-index: 10;
          fill: $primary;
          cursor: pointer;
          display: flex;
          align-items: center;
          justify-content: center;
          &:hover,
          &:focus,
          &:active {
             fill-opacity: .8;
          }
        }
        .svg-star  {
            fill: $primary;
            width: 40px;
            height: 32px;
           }
        
        .button-prev {
          left: 45%;
          right: auto;
          @include tablets {
            left: 38%;
          }
        }
         
        .button-next{
          right: 10px;
          left: 51%;
          @include tablets {
            left: 52%;
          }
        }
}


.frequently {
    .btn {
       display: block;
       border: none;
    }
    .accordion {
        .card {
            border-top: 1px solid $primary;
            label {
                cursor: pointer;
            }
            &--last {
                border-bottom: 1px solid $primary; 
            }
        }
        span {
            letter-spacing: 4px;
            font-weight: 300;
            @include phones {
                letter-spacing: 1px;
            }
        }
        p {
           line-height: 2.222;
           font-weight: 300;
           @include phones {
               font-size: 0.875rem;
               line-height: 1.8;
           }
        }
        svg {
            fill: $primary;
            padding-top: 2px;
        }
        .minus {
            display: none;
        }
    }
}

.is-hidden {
	display: none;
}
.icon-change {
	position: relative;
}
.icon-change:before {
	transform: translate(-50%,-50%) rotate(-90deg);
    will-change: translate,rotate;
}
.icon-change:after {
	transform: translate(-50%,-50%);
    will-change: translate,opacity;

}
.icon-change.open:before,
.icon-change.close:before { 
    transform:translate(-50%,-50%) rotate(-0deg);
    width:0;
}
.icon-change:after,
.icon-change:before {
	content: "";
	width: 14px;
	height: 1px;
	border-radius: 2px;
	background-color: $primary;
	display: block;
	position: absolute;
	top: 50%;
	transition: all .2s ease-in-out;
	opacity: 1;
	left: 7px;
	contain: strict;
}

.icon-change.close:before {
    content: "";
	width: 14px;
	height: 1px;
	border-radius: 2px;
	background-color: red;
	display: block;
	position: absolute;
	top: 50%;
	transition: all .2s ease-in-out;
	opacity: 1;
	left: 7px;
	contain: strict;
}

.testimonials {
     background-color: #747668;
    .carousel-item {
        overflow: hidden;
        &::before {
            content: "";
            display: block;    
            width: 100%;
            height: 100%;    
            position: absolute;
            top: 0;      
            left: 0;   
            background: transparent;
            background-image: linear-gradient(to bottom, rgba(0,0,0, 0.8), rgba(110, 119, 74, .5));
            opacity: 0.8;
            z-index: 1;
        }
        img {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            min-height: 100%;
            min-width: 100%;
        }
    }
    .line-health {
        font-size: 24px;
        font-family: "Oswald";
        color: $white;
        text-transform: uppercase;
        line-height: 2.5;
        font-weight: 300;
        letter-spacing: 3px;
        position: absolute;
        left: -2%;
        top: 75%;
        z-index: 200;
        display: inline-block;
        transform: rotate(90deg);
        @include tablets {
            font-size: .7rem;
            top: 75%;
            left: -3%;
        }
    }
    .carousel-text {
            .text-absolute {
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            z-index: 20;
            line-height: 1.818;
            @include tablets {
                top: 40%;
            }
            p {
                font-size: 1.375rem;
                letter-spacing: 2.5px;
                font-weight: 300;
                opacity: .9;
                @include tablets {
                    font-size: 1rem;
                }
                @include phones {
                    font-size: .875rem;
                }
            }

        }
    }
    .svg-star {
        fill: #aab384;
        opacity: 1;
    }
    .carousel-control-prev,
    .carousel-control-next {
        @include tablets {
            width: 50px;
            height: 50px;
            span {
                width: 30px;height: 30px;
            }
        }
    }
    .carousel-control-prev {
        @include tablets {
            left: calc(50% - 49px);
        }
    }
    .carousel-control-next {
        border-left: none;
        @include tablets {
            right: calc(50% - 49px);
        }
    }
}


.footer {
    letter-spacing: 3px;
    line-height: 2.857;
    @include phones() {
        letter-spacing: 1.5px;
        line-height: 2;
    }
    &__form {
        &__input:focus:invalid {
            border-bottom: 1px solid rgb(98, 2, 2);
        }
          
        &__input:valid {
            border-bottom: 1px solid blue;
          }
        &__input:invalid {
            border-bottom: $b-size solid rgba(111, 119, 129, .9);
        }
        #basic-addon1 {
            .arrow-next {
                fill: $primary;
                width: 30px;
                height: 25px;
                &:hover,
                &:focus, 
                &:active {
                    transform: translate(10px, 0);
                    fill: $teal;
                }
            }
        }
    }
    &__links {
        border-top: 2px solid $primary;
        white-space: nowrap;
        @include tablets() {
            border-top: none;
        }
        &--two-link {
            & a:first-child {
                &::after {
                    content: '/';
                    margin-left: 6px;
                }
            }
        }
    }
    &__logo {
        font-size: 60px;
        top: 0;
        left: 50%;
        transform: translate(-50%, -50%);
        @include tablets {
            font-size: 8vw;
        }
    }
    &__contacts {
       .after::after {
            content: '';
            display: block;
            position: absolute;
            width: 55px;
            height: 2px;
            background-color: $primary;
            top: 30%;
            left: 48%;
            transform: translate(-50%, -50%) rotate(90deg);
            @include phones {
                width: 35px;
            }
        }
        
    }
    .socials {
        white-space: nowrap;
        a:hover,
        a:focus,
        a:active {
            opacity: .8;
            transform: scale(1.1);
            text-shadow: 0.5rem 0.5rem 2px $black;
        }
        
        i {
            height: 40px;
            line-height: 40px;
        }
    }
   
    
}



// Modal-login

.modal {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0,0,0,0.8);
    z-index: 9999;     
    transition: opacity 400ms ease-in;
    display: none;
    pointer-events: none;
    &:target {
        display: block;
        pointer-events: auto;
    }
    &__inner{
        width: 50vw;
        position: relative;
        margin: 10% auto;
        padding: 30px;
        border-radius: 10px;
        background: #fff;
        @include phones() {
            width: 90vw;
        }
        .modal-close {
            background: $white;
            color: $teal;
            line-height: 40px;
            font-size: 25px;
            position: absolute;
            right: -12px;
            text-align: center;
            top: -10px;
            width: 40px;
            text-decoration: none;
            font-weight: 400;
            border-radius: 30px;
            box-shadow: 1px 1px 3px #000;
            &:hover {
                background: $teal; 
                color: $white;
            }
        }
    }
    input {
        border: none;
        font-family: inherit;
        font-size: inherit;
        margin: 0;
        outline: none;
    }
    input[type="submit"] {
         cursor: pointer; 
    }
    .clearfix {
        &:before,
        &:after {
            content: "";
            display: table;	
        }
        &:after {
            clear: both; 
        }
    }
}


#login-form {
    margin: 100px auto;
    max-width: 400px;
    fieldset {
        &::before {
            background-color: $white;
	        content: "";
            height: 10px;
            width: 10px;
	        left: 50%;
	        margin: -4px 0 0 -4px;
	        position: absolute;
	        top: 0;
		    transform: rotate(45deg);
	    }
    }
    input {
        font-size: 16px;
        [placeholder] {
            color: #000000;
        }
    }
    [type="email"],
    [type="password"] {
        border: 1px solid $teal;
        padding: 12px 10px;
        width: 100%;
    }
    [type="email"] {
        border-radius: 3px 3px 0 0;
    }
    [type="password"] {
        border-top: none;
        border-radius: 0px 0px 3px 3px;
    }
    [type="submit"] {
        background: $primary;
        border-radius: 3px;
        color: $white;
        float: right;
        font-weight: bold;
        min-width: 100px;
        padding: 12px 20px;
        &:hover {
             background: $teal; 
            }
    }
    .info {
        background: #e5e5e5;
        border-radius: 50%;
        display: inline-block;
        height: 20px;
        line-height: 20px;
        margin: 0 10px 0 0;
        text-align: center;
        width: 20px;
    }
   
}




@media screen and (max-width: 568px) {
    .about {
    .absolute {
        transform: translate(0, 0);
     }
    }
         
}







  



  

 

  
  