@mixin animation($animation) {
  -webkit-animation: #{$animation};
     -moz-animation: #{$animation};
       -o-animation: #{$animation};
          animation: #{$animation};
}

@mixin animation-delay($animation-delay) {
  -webkit-transition-delay: #{$animation-delay};
     -moz-transition-delay: #{$animation-delay};
       -o-transition-delay: #{$animation-delay};
          transition-delay: #{$animation-delay};
}

@mixin keyframes($keyframes) {
  @-webkit-keyframes #{$keyframes} { @content; }
     @-moz-keyframes #{$keyframes} { @content; }
       @-o-keyframes #{$keyframes} { @content; }
          @keyframes #{$keyframes} { @content; }
}

@mixin transform($transform) {
  -webkit-transform: $transform;
     -moz-transform: $transform;
      -ms-transform: $transform;
       -o-transform: $transform;
          transform: $transform;
}

@mixin transition($transition...) {
  -webkit-transition: #{$transition};
     -moz-transition: #{$transition};
       -o-transition: #{$transition};
          transition: #{$transition};
}

@mixin transition-delay($transition-delay) {
  -webkit-transition-delay: #{$transition-delay};
     -moz-transition-delay: #{$transition-delay};
       -o-transition-delay: #{$transition-delay};
          transition-delay: #{$transition-delay};
}

.hidden { 
  display: none; 
}

//Params
$header-height: 3.75rem;
$nav-toggle-height: 3rem;
$side-icon-padding: .8rem;
$icon-width: 2rem;
$trans-bounce: all cubic-bezier(0.47, 0.07, 0.21, 1.4) .2s;
$breakpoint: 768px;

// Animations

@keyframes hoverIn {
  from { transform: translateX(-100%); }
  to { transform: translateX(0); }
}

@keyframes hoverOut {
  from { transform: translateX(0); }
  to { transform: translateX(110%); }
}

@keyframes fadeInRight {
  0% {
      opacity: 0;
      left: 50px;
  }
  100% {
      opacity: 1;
      left: 0;
      transform: rotate(0deg);
  }
}


.header-nav.scrolled {
  background-color: rgba(255, 255, 255, .8);
  transition: background-color 200ms linear;
  border-bottom: 1px solid rgba(28, 28, 27, 0.5);
  ul li a,
  ol li a,
  ul li button {
    color: $secondary;
    font-weight: normal;
    &:hover,
    &:focus {
       color: $primary;
      }
  }
  .functions a,
  .functions button {
    border-left: 1px solid rgba(28, 28, 27, 0.5);
    }
  .function .search-icon {
        fill: $secondary;
        stroke: $secondary;
        &:hover,
        &:focus {
          transform: scale(1.2);
          fill: $primary;
          stroke: $primary;
        }
    }
  .function a:hover .search-icon {
       transform: scale(1.2);
       fill: $primary;
       stroke: $primary;
    }
  .header-nav__logo a {
      background-color: $secondary;
      border-radius: 50%;
  }
}


header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 2;
  }

  .header {
   &__logo {
    a {
      font-size: 45px;
      text-shadow: 1px 1px 4px $black;
      cursor: pointer;
    }
  }
}

.header-nav { 
  display: flex;
  min-height: 100%;
  font-size: 1rem;
  color: $white;
  border-bottom: 1px solid $white;
  &__list {
      display: flex;
      height: 100%;
      width: 100%;
  }
  &__item {
    height: 100%;
    &__link {
       position: relative;
       display: flex;
       align-items: center;
       width: 100%;
       height: 100%;
       color: white;
       font-size: 1rem;
       overflow: hidden;
       &:hover,
       &:focus {
         color: rgba(255, 255, 255, .7);
         transform: scale(1.03);
       }
       .header-nav__list & {
         &:after {
             content: '';
             position: absolute;
             top: 50%;
             left: 0;
             display: block;
             width: 100%;
             height: 3px;
             background-color: #ced6af;
             transform: translateX(-100%);
             animation: hoverOut .3s cubic-bezier(.77,0,.175,1) both;
            }
            &:hover:after {
               animation: hoverIn .3s cubic-bezier(.77,0,.175,1) both;
            }
         }
       }
    }
    &__search {
        display: none;
        width: 100%;
        height: 100%;
        position: absolute;
        right: 0;
        top: 0;
        background-color: $white;
        background-image: linear-gradient(to bottom,#1b6670 0,#427932 100%);
        align-items: center;
        &.is-active { 
            display: flex; 
            border-bottom: 1px solid transparent;
        }
    }
}

.functions {
    width: 100%;
    .function {
      display: flex;
        &:nth-child(1) {
            width: 27.5%;
          }
          &:nth-child(2) {
            width: 40%;
            display: flex;
            .search-icon {
              fill: $white;
            }
         }
        &:nth-child(3) {
            width: 32.5%;
            white-space: nowrap;
        }
    }
    .function a:hover .search-icon {
      transform: scale(1.3);
      stroke: $white;
    }
}

.function 
    a, button {
      border-left: 1px solid $white;
      }

  
.search {
  &__form {
     position: relative;
     display: flex;
     height: 2.5rem;
     width: 100%;
     background-image: linear-gradient(to bottom,#cad3ca 0,#70756f 100%);
     &__text {
       width: 98%;
       height: 100%;
       margin: 0 auto;
       background-color: transparent;
       border: none;
       outline: 0;
       padding-left: .8rem;
       color: white;
       [placeholder] {
           color: $white;
            opacity: .7;
        }
     }
     &__submit {
      position: absolute;
      top: 0;
      right: 1.5rem;
      height: 100%;
      width: 24px;
      background-image: url("https://www.playstation.com/fr-fr/1.22.03/etc/designs/pdc/clientlibs_base/images/searchbox/icon-magnifine-small-white-2x.png");
      background-repeat: no-repeat;
      background-color: transparent;
      background-size: 20px 20px;
      background-position: center center;
      border: 0;
      text-indent: -999px;
     }
    
  }
}

 
.search-close {
  display: flex;
  height: 100%;
  width: 3.7875rem;
  margin-left: auto;
  border: none;
  background: url("https://www.playstation.com/fr-fr/1.22.03/etc/designs/pdc/clientlibs_base/images/nav/nav-sprite.png") center -52px no-repeat;
  background-size: cover;
  outline: 0;
}



@mixin transition {
  @for $i from 1 through 50 {
    &:nth-child(#{$i}) {
      transition: transform 1s #{$i * .08}s cubic-bezier(.29,1.4,.44,.96);
    }
  }
}

@mixin alpha-attribute($attribute, $color, $background) {
  $percent: alpha($color) * 100%;
  $opaque: opacify($color, 1);
  $solid-color: mix($opaque, $background, $percent);
  #{$attribute}: $solid-color;
  #{$attribute}: $color;
}

$main: $teal;
$text: $white;
$contrast: darken($main, 50%);



.drawer-list {
  position: fixed;
  right: 0;
  top: 0;
  height: 100vh;
  width: 100vw;
  transform: translate(100vw, 0);
  -ms-transform: translatex(-100vw);
  box-sizing: border-box;
  padding-top: 145px;
  transition: width 475ms ease-out, transform 450ms ease, border-radius .8s .1s ease;
  border-bottom-left-radius: 100vw;
  @include alpha-attribute('background-color', rgba($contrast, 0.8), rgb(46, 42, 42));
  @media (min-width: 768px) {
    width: 80vw;
  }
    .menuBurger {
      width: 100%;
      margin: 0;
      padding: 0;
      overflow: auto;
      overflow-x: hidden;
      cursor: pointer;
      &__item {
        list-style: none;
        text-transform: uppercase;
        pointer-events: auto;
        white-space: nowrap;
        box-sizing: border-box;
        transform: translatex(100vw);
        -ms-transform: translatex(-100vw);
        &:last-child {
          margin-bottom: 2em;
        }
         }
        &__link {
          text-decoration: none;
          color: $text;
          text-align: center;
          display: block;
          padding: 1rem;
          font-size: 30px;
          &:hover {
            cursor: pointer;
            outline: 1px solid rgba(255, 255, 255, 0.3);
            background-image: linear-gradient(to bottom, #e6a835 ,#07c827);
          }
        }
    }
    .actions {
      width: 100%;
      justify-content: space-between;
      &__item {
        white-space: nowrap;
        text-align: center;
        font-size: 20px;
        color: $white;
        text-transform: uppercase;
        padding: .5rem 1rem;
        cursor: pointer;
        border: 1px solid $white;
        &:hover,
        &:active,
        &:focus {
          outline: 1px solid rgba(255, 255, 255, 0.3);
          background-image: linear-gradient(to bottom, #e6a835 ,#07c827);
        }
        .search-icon {
          fill: $white;
        }
     }
   }
         
  }
  
  


input.hamburger {
  display: none;
  &:checked {
    & ~ .drawer-list {
       transform: translatex(0);
        border-bottom-left-radius: 0;
        li {
          transform: translatex(0);
          @include transition;
          a {
            padding-right: 15px;
          }
        }
    }
    & ~ label {
      > i {
        background-color: transparent;
        transform: rotate(90deg);
        box-shadow: none;
        &:before {
          transform: translate(-50%, -50%) rotate(315deg);
        }
        &:after {
          transform: translate(-50%, -50%) rotate(-315deg);
        }
      }
     .close {
        color: $text;
        width: 100%;
      }
     .open {
        color: rgba(0, 0, 0, 0);
        width: 0;
      }
    }
  }
}

label.hamburger {
  z-index: 99;
  display: block;
  height: 40px;
  width: 40px;
  @media (min-width: 768px) {
    width: 50px;
    height: 50px;
  }
  &:hover {
    cursor: pointer;
  }
  .text {
    .close,
    .open {
      text-transform: uppercase;
      font-size: .8rem;
      letter-spacing: 1px;
      position: absolute;
      transform: translateY(50px);
      text-align: center;
      overflow: hidden;
      transition: width .25s .35s, color .45s .35s;
      box-shadow: none;
    }
    .close {
      color: rgba(0, 0, 0, 0);
      right: 0;
      width: 0;
    }
    .open {
      color: $text;
      width: 100%;
      text-shadow: 1px 1px 3px $black;
    }
  }
  > i {
    position: absolute;
    width: 100%;
    height: 2px;
    top: 50%;
    background-color: $text;
    box-shadow: 2px 2px 5px 0 $black;
    pointer-events: auto;
    transition-duration: .35s;
    transition-delay: .35s;
    &:before,
    &:after {
      position: absolute;
      display: block;
      width: 100%;
      height: 2px;
      left: 50%;
      background-color: $text;
      box-shadow: 2px 2px 5px 0 $black;
      content: "";
      transition: transform 0.35s;
      transform-origin: 50% 50%;
    }
    &:before {
      transform: translate(-50%, -14px);
    }
    &:after {
      transform: translate(-50%, 14px);
    }
  }
}

label.hamburger {
  position: fixed;
  top: 3%;
  right: 7%;
}


@media screen and (min-width: 993px) {
.header__logo  {
  display: none;
}
.header-nav {
  display: flex;
}
label.hamburger {
  display: none;
}
}

@media screen and (max-width: 992px) {
  .header-nav {
    display: none;
  }
  label.hamburger {
    display: block;
  }
  
}























